<template>
  <div class="project-manage">
    <a-button type="primary" @click="onDetailModal"></a-button>
    <div class="item-detail">
      <a-modal title=""
               v-model="itemDetailModal"
               okText=""
               cancelText="">
        <h3>:</h3>
        <p>:</p>
        <p>:</p>
      </a-modal>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UrlList',
  data () {
    return {
      itemDetailModal: true
    }
  },
  methods: {
    onDetailModal () {
      this.itemDetailModal = true
    }
  }
}
</script>

<style lang="stylus" scoped>
</style>



